import React, { useCallback, useState } from "react"
import Header from "../components/header"
import Layout from "../components/layout"
import {graphql} from "gatsby"
import {Collapse} from "react-collapse" 

import styles from "./projects.module.css"

function ProjectSection({node}) {
  const [isExpanded, setIsExpanded] = useState(false) ; 

  const onClickExpand = useCallback(
    () => setIsExpanded(!isExpanded), 
    [isExpanded]
  )

  return (
    <div>
      <h2 className={styles.projectTitle}>{node.frontmatter.title}</h2>
      <p>{node.frontmatter.description}</p>
      <Collapse isOpened={isExpanded}>
        <div dangerouslySetInnerHTML={{ __html: node.html}}></div>
      </Collapse>
      <button onClick={onClickExpand}>{isExpanded ? "Hide" : "Show more"}</button>
      <hr className={styles.hr}/>
    </div>
  )

  return (
    <div>
      <h2 className={styles.projectTitle}>{node.frontmatter.title}</h2>
      <p>{node.excerpt}</p>
      <button onClick={onClickExpand}>{}</button>
      <Collapse isOpened={isExpanded}>
        <div>
          <p>THIS IS MORE !</p>
        </div>
      </Collapse>
    </div>
  )
}

export default function Projects({data}) {

  const projectsSection = data.allMarkdownRemark.edges.map(({node}) => 
    <ProjectSection node={node} />
  )

  return (
    <Layout>
      <Header headerText="Discover some of my projects"/>
      <p>Here is a selection of some projects I have come to realize. For some of them, more documentation is available under the github link !</p>
      <p className={styles.footnote}>Number of projects: {data.allMarkdownRemark.totalCount}</p>
      {projectsSection}
    </Layout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            description
          }
          excerpt
          html
        }
      }
    }
  }
  `